import axios from 'axios'
import React, { useEffect, useState } from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';


export default function CategorySlider() {
   
          
       let [CategoryList , setCategoryList] = useState([])
       useEffect(()=>{

        getCategory()
       },[])
      async function getCategory()
       {
        let req = await axios.get("https://ecommerce.routemisr.com/api/v1/categories")
        console.log(req.data.data)
        setCategoryList(req.data.data)
       }
       return <>
       <h2 className='py-3'>Categories: </h2>
       <OwlCarousel items={7} loop autoplay={true} autoplayTimeout={2000}>
        {CategoryList.map((e)=> <div className='item'  key={e._id}>
            <img src={e.image} height={220} alt="" />
        </div>)}
       </OwlCarousel>
       
       
       </>

}
