import React, { useContext } from 'react';
import Style from "./ProductDetails.module.css";
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';
import { CartContext } from '../../CounterContext/CartContext';
import toast from 'react-hot-toast';


export default function ProductDetails() {

  let { addToCart } = useContext(CartContext)

  async function postToCart(id) {
    let { data } = await addToCart(id)
    if (data.status == "success") {
      toast.success(data.message, {
        duration: 4000,
        position: 'top-center'
      })
    }
  }

 
  let param = useParams()
  // console.log(param.id)
  function getDetails(id) {
    return axios.get(` https://ecommerce.routemisr.com/api/v1/products/${id}`)
  }
  let { isError, isLoading, isFetching, data } = useQuery("productDetails", () => getDetails(param.id))
  // console.log(data?.data.data)
  function getSrc(e)
  {
    let imgPath = e.target.getAttribute("src");
    console.log(imgPath)
    document.querySelector("#myImage").setAttribute("src" , imgPath)
  }

  return <>
  
    {data?.data.data ? <div className="row align-items-center pt-5">

      <div className="col-md-4 ">
        <div className="row align-items-center g-0">
          <div className="col-md-2">
            {data?.data.data.images.map((el)=> <img src={el} onClick={getSrc} className='w-100' alt="" />)}
           


          </div>
          <div className="col-md-10">

            <img src={data?.data.data.imageCover} id='myImage' className='w-100' alt="" />
          </div>
        </div>
      </div>
      <div className="col-md-8 ">
        <h4 className=''> {data?.data.data.title} </h4>

        <p> {data?.data.data.description} </p>
        <h5 className=''>{data?.data.data.category?.name}</h5>
        <div className="d-flex justify-content-between">
          <span className='text-main'> Price: {data?.data.data.price} EGP</span>
          <span><i className='fa-solid fa-star rating-color'></i> {data?.data.data.ratingsAverage}</span>
        </div>
        <button className='btn bg-main text-white  w-100 mt-3' onClick={() => postToCart(param.id)}>Add Product</button>
      </div>
    </div> : ""}




  </>
}
