import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'

export default function ForgetPassword() {
    let [errmsg, seterrmsg] = useState("")
    let [formCode, setformCode] = useState(true)

   let navigate =  useNavigate()
    let validationSchema = Yup.object({
        email: Yup.string().email("email is invalid").required("email is required"),
    })

    let validationSchema2 = Yup.object({
        resetCode: Yup.string().matches( /^[0-9]{5,6}$/ , "resetCode is invalid").required("resetCode is required"),
    })

    let Formik = useFormik({
        initialValues: {
            email: ""
        },
        validationSchema: validationSchema,
        onSubmit: ForgetPasswordApi
    })


    let Formik2 = useFormik({
        initialValues: {
            resetCode: ""
        },
        validationSchema: validationSchema2,
        onSubmit:resetCodeApi
    })


    async function ForgetPasswordApi(value) {
        console.log(value)
        let req = await axios.post("https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords", value)
            .catch((err) => {
                seterrmsg(err.response.data.message)
            })
        if (req.data.statusMsg == "success") {
            setformCode(false)
        }
        console.log(req)
    }

    async function resetCodeApi(value) {
        console.log(value)
        let req = await axios.post("https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode", value)
            .catch((err) => {
                seterrmsg(err.response.data.message)
            })
            if(req.data.status =="Success")
            {
                navigate("/ResetPassword")

            }
     
    }
    return <>
        {formCode ? <form action="" onSubmit={Formik.handleSubmit} className='mt-5 py-5'>
        {errmsg ? <div className="alert alert-danger">{errmsg}</div> : ""}
            <label htmlFor="email" className='py-1'>Enter your Email : </label>
            <input type='email' id='email' name='email' value={Formik.values.email} onChange={Formik.handleChange} onBlur={Formik.handleBlur} className='form-control ' />
            <button className='btn bg-main text-white mt-3' type='submit' > send code</button>

        </form> : <form  onSubmit={Formik2.handleSubmit}  className='mt-5 py-5'>
            <label htmlFor="resetCode" className='py-1'>Enter your resetCode : </label>
            <input type='text' value={Formik2.values.resetCode} id='resetCode' name='resetCode' onBlur={Formik2.handleBlur} onChange={Formik2.handleChange} className='form-control ' />
          {Formik2.errors.resetCode && Formik2.touched.resetCode ? <div className="alert alert-danger">{Formik2.errors.resetCode}</div> : "" }
          
            <button className='btn bg-main text-white mt-3' type='submit' > Verify code</button>

        </form>}





    </>
}
