import axios from "axios";
import { createContext } from "react";

export let CartContext = createContext();
export default function CartContextProvider(props) {
  let headers = { token: localStorage.getItem("userToken") };
  function addToCart(productId) {
    return axios
      .post(
        "https://ecommerce.routemisr.com/api/v1/cart",
        {
          productId,
        },
        {
          headers,
        }
      )
      .then((response) => response)
      .catch((error) => error);
  }


  function getLogedCart() {
    return axios.get("https://ecommerce.routemisr.com/api/v1/cart", {
      headers
    }).then((response) => response)
      .catch((error) => error)
  }
  function removeCart(productId) {
    return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`, { headers })
      .then((response) => response).catch((error) => error)
  }

  function updateCart(productId, count) {
    return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`,
      { count }, { headers }).then((response) => response).catch((error) => error)
  }

  function clearCart() {
    return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`, { headers })
  }

function payment(id, data)
{
  let body =
    {
      shippingAddress : data

    }
  return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=http://localhost:3000` , body, {headers} )
}



  return (
    <CartContext.Provider value={{  payment,  addToCart, getLogedCart, removeCart, updateCart, clearCart }}>
      {props.children}
    </CartContext.Provider>
  );
}
