import React, { useContext, useEffect, useState } from 'react';
import axios from 'axios';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';
import { CartContext } from '../../CounterContext/CartContext';
import toast from 'react-hot-toast';



export default function FeatureProduct() {
    let {addToCart} = useContext(CartContext)

 async function postToCart(id)

{
    let {data} = await addToCart(id)
    if(data.status=="success")
    {
        toast.success(data.message , {
             duration: 4000,
            position: 'top-center'})
    }

}


    function getAllData() {
        return axios.get("https://ecommerce.routemisr.com/api/v1/products")
    }
    let { data, isFetching, isLoading, isError, refetch } = useQuery("productFeature", getAllData, {
        // cacheTime:400,
        // refetchOnMount:false,
        // staleTime:500,
        // refetchInterval:500
        // enabled:false
    })


    return <>
  



        {isLoading ? <div className="d-flex justify-content-center align-items-center position-fixed top-0 start-0 end-0 bottom-0 loading bg-white">
            <span className="loader"></span>
        </div> :
            <div className="container py-5 mt-5">
                {/* <button onClick={()=>{refetch()}} className='btn bg-main text-white  w-100 '>Get Product</button> */}

                <div className="row g-2 ">
                    {data?.data.data.map((el) => {
                        return <div className="col-md-2 " key={el.id} >
                            
                                <div className="product p-2 " >
                                <Link to={`/productDetails/${el.id}`}>
                                    <img src={el.imageCover} className='w-100' alt="" />
                                    <h6 className="text-main">{el.category.name}</h6>
                                    <h5>{el.title.split(" ").slice(0, 2).join(" ")}</h5>
                                    <div className="d-flex justify-content-between">
                                        <span>{el.price}EGP</span>
                                        <span><i className='fa-solid fa-star rating-color'></i> {el.ratingsAverage}</span>
                                    </div>
                                </Link>
                                    <button onClick={()=>postToCart(el.id)} className='btn bg-main text-white  w-100 '>Add Product</button>
                                </div>
                           
                        </div>
                    })}

                </div>
            </div>
        }





    </>
}
