import React, { useState } from 'react';
import Style from "./Register.module.css";
import { useFormik } from 'formik';
import * as Yup from "yup";
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';

export default function Register() {


  let navigate = useNavigate()
  let [loading, setloading] = useState(false)
  let [apiError, setapiError] = useState("")


  async function RegisterSubmit(value) {
    setloading(true)
    let data = await axios.post("https://ecommerce.routemisr.com/api/v1/auth/signup", value)
      .catch((err) => {
        setapiError(err.response.data.message)
        setloading(false)
        
      })
    if(data.message == "success")
    {
      setloading(false)
      navigate("/login")
    }
    navigate("/login")
  }

  let validationSchema = Yup.object({
    name: Yup.string().min(3, "min char 3").max(10, "max char 10").required("name is required"),
    email: Yup.string().email("email is invalid").required("email is required"),
    phone: Yup.string().matches(/^01[0-2]\d{1,8}$/, "phone is not correct").required("phonne is required"),
    password: Yup.string().matches(/^[a-zA-Z0-9]{6,}$/, "password should be more than 6 char").required("password is required"),
    rePassword: Yup.string().oneOf([Yup.ref("password")], "repassword not match password")


  })
  let Formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      phone: "",
      password: "",
      rePassword: ""
    },
    validationSchema: validationSchema,
    onSubmit: RegisterSubmit
  })

  return <>
    <div className="w-75 mx-auto py-5 mt-5 ">
      <h2>Register Now..</h2>
      <form onSubmit={Formik.handleSubmit}>
        {apiError ? <div className="alert alert-danger ">{apiError}</div>
          : ""}
        <label htmlFor="name" className='py-1'>Name : </label>
        <input type='text' id='name' name='name' value={Formik.values.name} onChange={Formik.handleChange} onBlur={Formik.handleBlur} className='form-control ' />
        {Formik.errors.name && Formik.touched.name ? <div className='alert alert-danger py-2'>{Formik.errors.name}</div> : ""}


        <label htmlFor="email" className='py-1'>Email : </label>
        <input type='email' id='email' name='email' value={Formik.values.email} onChange={Formik.handleChange} onBlur={Formik.handleBlur} className='form-control ' />

        {Formik.errors.email && Formik.touched.email ? <div className='alert alert-danger py-2'>{Formik.errors.email}</div> : ""}




        <label htmlFor="phone" className='py-1'>phone : </label>
        <input type='tel' id='phone' name='phone' value={Formik.values.phone} onChange={Formik.handleChange} onBlur={Formik.handleBlur} className='form-control ' />

        {Formik.errors.phone && Formik.touched.phone ? <div className='alert alert-danger py-2'>{Formik.errors.phone}</div> : ""}


        <label htmlFor="password" className='py-1'>password : </label>
        <input type='password' id='password' name='password' value={Formik.values.password} onChange={Formik.handleChange} onBlur={Formik.handleBlur} className='form-control ' />

        {Formik.errors.password && Formik.touched.password ? <div className='alert alert-danger py-2'>{Formik.errors.password}</div> : ""}


        <label htmlFor="rePassword" className='py-1'>rePassword : </label>
        <input type='password' id='rePassword' name='rePassword' value={Formik.values.rePassword} onChange={Formik.handleChange} onBlur={Formik.handleBlur} className='form-control ' />
        {Formik.errors.rePassword && Formik.touched.rePassword ? <div className='alert alert-danger py-2'>{Formik.errors.rePassword}</div> : ""}
<div className="d-flex align-items-center  ">
  
{loading ? <button type='button' className='btn btn-success '><i className='fas fa-spinner fa-spin'></i></button> : 

<button type='submit' disabled={!(Formik.isValid && Formik.dirty)} className='btn btn-success mt-3'>Register</button>}
        <Link to={"/login"} className=' text-decoration-none text-dark mt-3 px-2'> Login Now </Link>
</div>
      </form>
    </div>

  </>
}
