import React from 'react';
import Layout from './component/Layout/Layout';
import Home from './component/Home/Home';
import Cart from './component/Cart/Cart';
import Brands from './component/Brands/Brands';
import Categories from './component/Categories/Categories';
import Login from './component/Login/Login';
import Register from './component/Register/Register';
import Notfound from './component/Notfound/Notfound';
import Product from './component/Product/Product';
import { RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom'
import CounterContextProvider from './CounterContext/CounterContext';
import CartContextProvider from './CounterContext/CartContext';
import UserContextProvider from './CounterContext/UserContxt';
import ProductDetails from './component/ProductDetails/ProductDetails';
import { Toaster } from 'react-hot-toast';
import GuardRouting from './component/GuardRouting/GuardRouting';
import ForgetPassword from './component/ForgetPassword/ForgetPassword';
import ResetPassword from './component/ResetPassword/ResetPassword';
import Allorders from './component/Allorders/Allorders';
import Checkout from './component/Checkout/Checkout';





let Routing = createHashRouter([
  {
    path: "/", element: <Layout />, children: [
      { index: "true", element: <GuardRouting><Home /> </GuardRouting> },
      { path: "cart", element: <GuardRouting><Cart /> </GuardRouting> },
      { path: "product", element: <GuardRouting><Product /></GuardRouting> },
      { path: "productDetails/:id", element: <GuardRouting><ProductDetails /></GuardRouting> },
      { path: "brands", element: <GuardRouting><Brands /></GuardRouting> },
      { path: "category", element: <GuardRouting><Categories /></GuardRouting> },
      { path: "allorders", element: <GuardRouting><Allorders /></GuardRouting> },
      { path: "checkout/:payId", element: <GuardRouting><Checkout /></GuardRouting> },
      { path: "forgetpassword", element: <ForgetPassword /> },
      { path: "ResetPassword", element: <ResetPassword /> },
      { path: "login", element: <Login /> },
      { path: "register", element: <Register /> },
      { path: "*", element: <Notfound /> },
    ]
  }
])





export default function App() {



  return <>
    <CartContextProvider>
      <UserContextProvider>
        <CounterContextProvider>
          <RouterProvider router={Routing}></RouterProvider>
        </CounterContextProvider>
      </UserContextProvider>
      <Toaster />
    </CartContextProvider>

  </>
}
