import React, { useContext } from 'react';
import Style from "./Checkout.module.css";
import { useParams } from 'react-router-dom';
import { useFormik } from 'formik';
import * as Yup from 'yup'
import { CartContext } from '../../CounterContext/CartContext';

export default function Checkout() {
  let {payment} =  useContext(CartContext)
  let data = useParams()
  console.log(data.payId)
  let validationSchema = Yup.object({
    phone: Yup.string().required('Phone is Requierd').matches(/^01[0125][0-9]{8}$/, 'Enter Valid Number '),
    city: Yup.string().required("City is Requierd").matches(/^[\w-]{3,}$/, 'Enter Valid City'),
    details: Yup.string().required("details is Requierd").matches(/^[\w-]{3,}$/, 'Enter Valid details')

  })


  let formik = useFormik({
    initialValues: {
      phone: '',
      city: '',
      details: '',
    },
    onSubmit: checkpayment,
    validationSchema
  })


  async function checkpayment(val) {
    let req = await payment(data.payId  , val)
    if(req.data.status=="success")
    {
       let URL= req.data.session.url
       window.open(URL , '_self')
    }
    console.log(req)
  }


  return <>
    <div className='w-75 mx-auto my-5'>

      <form onSubmit={formik.handleSubmit} >
        <div className="mb-3">
          <input type="text " onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control' placeholder='Enter city' name='city' />
          {formik.touched.city && formik.errors.city ? <p className='text-danger py-2'> {formik.errors.city} </p>
            : ""}
        </div>

        <div className="mb-3">
          <input type="text " onChange={formik.handleChange} onBlur={formik.handleBlur}  className='form-control' placeholder='Enter phone' name='phone' />
          {formik.touched.phone && formik.errors.phone ? <p className='text-danger py-2'> {formik.errors.phone} </p>
            : ""}
        </div>



        <div className="mb-3">
          <textarea onChange={formik.handleChange} onBlur={formik.handleBlur}  className='form-control' placeholder='Enter Details' name='details' ></textarea>
          {formik.touched.details && formik.errors.details ? <p className='text-danger py-2'> {formik.errors.details} </p>
            : ""}
        </div>
<div className="text-center">

          <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='bg-main text-white  btn w-100 ' > Pay <i className='fa-brands fa-cc-visa mx-2'></i>  </button>

</div>


      </form>
    </div>


  </>
}
