import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import * as Yup from "yup"


export default function ResetPassword() {
    let [errmsg, seterrmsg] = useState("")

    let nav = useNavigate()

    let validationSchema = Yup.object({
        email: Yup.string().email("email is invalid").required("email is requierd"),
        newPassword: Yup.string().matches(/^[a-zA-Z0-9]{6,}$/, "newPassword should be more than 6 char").required("newPassword is required"),
    })
    let formik = useFormik({
        initialValues: {
            email: "",
            newPassword: ""
        },
        onSubmit: ResetPasswordApi,
        validationSchema
    })

    async function ResetPasswordApi(value) {
        let req = await axios.put("https://ecommerce.routemisr.com/api/v1/auth/resetPassword", value).catch((err) => {
            seterrmsg(err.response.data.message)
        })
        console.log(req)
        if (req.data.token) {
            nav("/login")
        }

    }
    return <>

        {errmsg ? <div className="alert alert-danger">{errmsg}</div> : ""}

        <form onSubmit={formik.handleSubmit} className='mt-5 pt-5' >
            <label htmlFor="email" className='py-1'>Email : </label>
            <input type='email' id='email' name='email' value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control ' />

            {formik.errors.email && formik.touched.email ? <div className='alert alert-danger py-2'>{formik.errors.email}</div> : ""}

            <label htmlFor="newPassword" className='py-1'> newPassword : </label>
            <input type='password' id='newPassword' name='newPassword' value={formik.values.newPassword} onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control ' />

            {formik.errors.newPassword && formik.touched.newPassword ? <div className='alert alert-danger py-2'>{formik.errors.newPassword}</div> : ""}

            <button type='submit' disabled={!(formik.isValid && formik.dirty)} className='btn btn-success mt-3'>Update Password </button>






        </form>





    </>
}
