import React, { useContext, useEffect, useState } from 'react';
import Style from "./Cart.module.css";
import { CartContext } from '../../CounterContext/CartContext';
import { CounterContext } from '../../CounterContext/CounterContext';
import { Link } from 'react-router-dom';

export default function Cart() {
  let { getLogedCart, removeCart, updateCart, clearCart } = useContext(CartContext)
  let [cartDetails, setCartDetails] = useState(null)

  async function getCart() {
    let { data } = await getLogedCart()
    setCartDetails(data)


  }

  async function updateCartItem(id, count) {
    let { data } = await updateCart(id, count)
    setCartDetails(data)

  }
  async function removeCartItem(id) {
    let { data } = await removeCart(id)
    setCartDetails(data)
  }
  useEffect(() => {
    getCart();
  }, [])

  return <>
    {cartDetails ? <div className="w-75  mx-auto p-3 bg-main-light">
      <h3>Shopping Cart</h3>
      <h4 className='h6 text-main fw-bolder'>Cart Items : {cartDetails.numOfCartItems} </h4>
      <h4 className='h6 text-main fw-bolder my-2'>Cart price : {cartDetails.data.totalCartPrice} </h4>
      <button className='btn btn-danger my-3' onClick={() => clearCart()}>clear cart</button>

      {cartDetails.data.products.map((product) => {

        return <div className="row border-bottom p-2 " key={product.product.id}>
          <div className="col-md-1">
            <img src={product.product.imageCover} className='w-100' alt="" />
          </div>
          <div className="col-md-11">
            <div className="d-flex justify-content-between align-items-center">
              <div>
                <h3 className='h6'>{product.product.title.split(" ").slice(0, 3).join(" ")} </h3>
                <h6 className=' text-main'>Price : {product.price} EGP </h6>
              </div>
              <div>
                <button className=' btn py-1 btn-success ' onClick={() => updateCartItem(product.product.id, product.count + 1)}>+</button>
                <span className='mx-3'>{product.count}</span>
                <button className='btn py-1 btn-danger' onClick={() => updateCartItem(product.product.id, product.count - 1)}>-</button>
              </div>
            </div>
            <div className='d-flex align-items-center '>
              <button className='p-0 btn  ' onClick={() => removeCartItem(product.product.id)}>
                <i className='fas fa-trash-can font-sm text-danger '></i> <span className='px-1'>Remove</span>
              </button>

            </div>
          </div>

        </div>
      })}
      <Link to={'/checkout/'+ cartDetails.data._id }>
        <button className='btn bg-success text-white mt-3 '>CheckOut Payment !</button>
      </Link>



    </div>




      : <div className="d-flex justify-content-center align-items-center position-fixed top-0 start-0 end-0 bottom-0 loading bg-white">
        <span className="loader"></span>
      </div>}


  </>
}
