import React, { useContext } from 'react';
import Style from "./Navbar.module.css";
import { Link, useNavigate } from 'react-router-dom';
import { CounterContext } from '../../CounterContext/CounterContext';
import { UserContxt } from '../../CounterContext/UserContxt';

import logo from "../../images/freshcart-logo.svg"

export default function Navbar() {
let navigate=useNavigate()
  let {count} = useContext(CounterContext)
  let {userToken ,setUserToken} = useContext(UserContxt)

function logout()
{
  localStorage.removeItem("userToken");
  setUserToken(null)
  navigate('/login')
}


  return <>
  <nav className="navbar navbar-expand-lg bg-body-tertiary position-fixed top-0 end-0 start-0">
  <div className="container-fluid">
    <img className="navbar-brand" src={logo} alt=""  />
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
     <>
      
       {userToken !== null? <><ul className="navbar-nav me-auto mb-2 mb-lg-0">
       <li className="nav-item">
          <Link className="nav-link" to="/">Home {count}</Link>
        </li>
        {/* <li className="nav-item">
          <Link className="nav-link" to="/product">Product</Link>
        </li> */}
        <li className="nav-item">
          <Link className="nav-link" to="/cart">Cart</Link>
        </li>
        {/* <li className="nav-item">
          <Link className="nav-link" to="/brands">Brands</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/category">Category</Link>
        </li> */}
      
       
       </ul>
        </>:""} 
      
        
        
      
     </>
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        
      <li className="nav-item d-flex align-items-center">
        <i className='fab fa-facebook mx-2 cursor-pointer '></i>
        <i className='fab fa-twitter mx-2 cursor-pointer'></i>
        <i className='fab fa-instagram mx-2 cursor-pointer'></i>
        <i className='fab fa-tiktok mx-2 cursor-pointer'></i>
        <i className='fab fa-youtube mx-2 cursor-pointer'></i>
        </li>
        {userToken==null?<><li className="nav-item">
          <Link className="nav-link" to="/login">Login</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/register">Register</Link>
        </li></>:""}
        
        {userToken!==null?<li className="nav-item">
          <span onClick={()=>logout()} className="nav-link cursor-pointer">LogOut</span>
        </li>:""}
        
       
        
        
      </ul>
      
    </div>
  </div>
</nav>
  
  </>
}
