import React, { useContext, useState } from 'react';
import Style from "./Login.module.css";

import { useFormik } from 'formik';
import * as Yup from "yup";
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import { UserContxt } from '../../CounterContext/UserContxt';


export default function Login() {
  let navigate = useNavigate()
  let [loading, setloading] = useState(false)
  let [apiError, setapiError] = useState("")
  
  let {setUserToken} = useContext(UserContxt)

  async function LoginSubmit(value) {
    setloading(true)
    let {data} = await axios.post("https://ecommerce.routemisr.com/api/v1/auth/signin", value)
      .catch((err) => {
        setapiError(err.response.data.message)
        setloading(false)
      })

      console.log(data);
    if(data.message == "success")
    {
      setloading(false)
      localStorage.setItem("userToken" , data.token)
      setUserToken(data.token)
      
      navigate("/")
    }
    
  }

  let validationSchema = Yup.object({
    email: Yup.string().email("email is invalid").required("email is required"),
    password: Yup.string().matches(/^[a-zA-Z0-9]{6,}$/, "password should be more than 6 char").required("password is required"),


  })



  let Formik = useFormik({
    initialValues: {

      email: "",

      password: "",

    },
    validationSchema: validationSchema,
    onSubmit: LoginSubmit
  })

  return <>
    <div className="w-75 mx-auto py-5 mt-5">
      <h2>Login Now..</h2>
      <form onSubmit={Formik.handleSubmit}>
        {apiError ? <div className="alert alert-danger ">{apiError}</div>
          : ""}
        

        <label htmlFor="email" className='py-1'>Email : </label>
        <input type='email' id='email' name='email' value={Formik.values.email} onChange={Formik.handleChange} onBlur={Formik.handleBlur} className='form-control ' />

        {Formik.errors.email && Formik.touched.email ? <div className='alert alert-danger py-2'>{Formik.errors.email}</div> : ""}

        <label htmlFor="password" className='py-1'>password : </label>
        <input type='password' id='password' name='password' value={Formik.values.password} onChange={Formik.handleChange} onBlur={Formik.handleBlur} className='form-control ' />

        {Formik.errors.password && Formik.touched.password ? <div className='alert alert-danger py-2'>{Formik.errors.password}</div> : ""}


        
       <div className="d-flex align-items-center ">
       {loading ? <button type='button' className='btn btn-success '><i className='fas fa-spinner fa-spin'></i></button> : <button type='submit' disabled={!(Formik.isValid && Formik.dirty)} className='btn btn-success mt-3'>Login</button>}
        <Link to={"/register"} className=' text-decoration-none text-dark mt-3 px-2'> Register Now </Link>

       </div>
      </form>

      <div className='d-flex  justify-content-end'>
        <Link to={"/forgetpassword"} className=' text-decoration-none text-main '> Forget Password? </Link>

      </div>
    </div>

  </>
}

